.wrapper {
    width: 800px;
    margin: 30px auto;
}

.tabs {
    display: flex;
    justify-content: center;
}

.tabItem {
    width: auto;
    height: 20px;
    cursor: pointer;
    border: 2px solid;
    text-align: center;
    padding: 15px;
}

.tabItemInactive {
    opacity: 0.3;
    filter: grayscale(100%);
}

.tabItemTitle {
    color: rgb(0, 0, 0, 0.9);
    font-size: 16px;
    margin: 0;
}

.wrapper .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top : 1rem;
}

@media screen and (max-width:767px) {
    .wrapper {
        width: 100vw;
    }

    .tabs {
        display: flex;
        width: 100%;
    }
    
    .wrapper .content {
        width: 100vw;
        overflow-x: auto;
    }
}